class Pikachure {
	URL = "";

	constructor() {
		switch(process.env.NODE_ENV) {
			case "production": {
				this.URL = "https://pikachure-server.online"
				break;
			}
			
			default: 
			case "development": {
				this.URL = "https://pikachure-server.online"
				//this.URL = "http://192.168.1.109"
				this.URL = "http://localhost";
			}
		}
	}
}

export default new Pikachure();